.login {
  display: grid;
  place-items: center;
  height: 100vh;
  width: 100vw;
  /*background-color: black; */
}

.login > img {
  width: 50%;
}

.login > a {
  color: white;
  padding: 1rem;
  text-decoration: none;
  border-radius: 99px;
  font-weight: 500;
  font-size: 20px;
}

.dashboard_container {
  /* outline: 1px solid red; */
  display: grid;
  /* grid-template-columns: repeat(auto-fit, minmax(60rem, 90rem)); */
  grid-gap: 2rem;
  justify-content: center;
  padding: 1rem;
  width: 95%;
  height: 100%;
}

@media (min-width: 800px) {
  .dashboard_container {
    padding: 3rem;
  }
}

/* =============================Test======================================== */
.top20Row {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  background-color: #424242;
  /* background-color: #204064; */
  border-radius: 100px;
  box-shadow: 5px 5px 20px 0.5px rgba(0, 0, 0, 0.329);
}

.top20Row > * {
  flex-grow: 1;
}

.top20Img {
  display: flex;
  justify-content: center;
  max-width: 20%;
  padding: 10px 10px 10px 10px;
}
.top20Img img {
  border-radius: 50%;
  max-width: 100%;
  max-height: 100%;
  box-shadow: 5px 5px 20px 0.5px rgba(0, 0, 0, 0.087);
}
.songInfo {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-evenly;
}

.songMetrics {
  /* flex-basis: auto; */
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  color: white;
}

.metric {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.songName {
  color: #d85c27;
  max-width: 15vw;
}
.songArtist {
  /* font-size: medium; */
  font-size: calc(10px + 1vmin);
  color: white;
}
.stringName {
  align-self: flex-start;
}
.stat {
  align-self: flex-end;
  padding-right: 30px;
}
.rank {
  display: flex;
  flex-direction: column;
}
.top20Table {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  /* align-items: center; */
  justify-content: space-evenly;
  grid-row-gap: 20px;
  row-gap: 20px;

  /* max-width: 90%; */
}
.searchResultsContainer {
  /* outline: 1px solid red; */
  display: inherit;
  /* grid-template-columns: repeat(auto-fit, minmax(60rem, 90rem)); */
  grid-gap: 2rem;
  justify-content: center;
  padding: 1rem;
  width: 95%;
  height: 50%;
}
.topResults {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  grid-row-gap: 20px;

  row-gap: 20px;


  /* max-width: 90%; */
}

.card {
  overflow: hidden;
  box-shadow: 0px 2px 20px black;
  border-radius: 3rem;
  display: flex;
  flex-direction: column;
  background-color: rgb(106, 106, 106);

  transition: -webkit-transform 200ms ease-in;

  transition: transform 200ms ease-in;

  transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}

.image {
  height: 15vh;
  width: 100%;
  object-fit: cover;
  border-radius: 10rem;
}

.title {
  padding: 1rem;
}

.description {
  padding: 1rem;
}
.rank {
  padding: 0.5rem;
}

